import styled, {css} from 'styled-components'
import { Link } from "react-router-dom"


const SADStyledPreviousLink = styled(Link)`

  font-family: inherit;
  background-color: #007079;
  border: none;
  color: white;
  padding: .5em 1em;
  border-radius: .2em;
  font-size: .9em;
  cursor: pointer;
  text-decoration: none;
  width: 100px;
  text-align: center;

`

export default SADStyledPreviousLink
